.container1 {
    background-color: #1e1e1e;
    padding: 30px 30px 90px; /* Combined padding */
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    text-align: center;
    max-height: 80vh;
    overflow-y: auto;
    margin: 0 auto;
    position: relative;
    min-width: 350px;
    width: 100%;
  }
  
  .input-group {
    margin-bottom: 20px;
  }
  
  .spell-item {
    margin-bottom: 10px;
  }
  
  .pressable {
    cursor: pointer;
    color: #008080;
    text-decoration: underline;
  }
  
  .pressable:hover {
    color: #026b6b;
  }
  
  .description {
    text-align: left; /* Align text to the left */
    margin-left: 20px; /* Add some left margin for better visibility */
    white-space: pre-line; /* Preserve whitespace formatting */
    font-size: 0.9em; /* Slightly smaller font size for descriptions */
    max-width: 30vw;
  }

  @media (max-width: 768px) {
    .container1 {
      padding: 20px 15px 60px; /* Reduce padding for smaller screens */
      max-height: 90vh; /* Allow more height on mobile */
      min-width: 100%; /* Ensure it takes full width on small screens */
    }
  
    .input-group {
      text-align: center; /* Center align text for better mobile display */
    }
  
    .description {
      margin-left: 0; /* Remove left margin on mobile */
      max-width: 100%; /* Allow full width for descriptions */
    }
  }
  
  @media (max-width: 480px) {
    .container1 {
      padding: 15px 10px 50px; /* Further reduce padding for very small screens */
    }
  
    .input-group {
      margin-bottom: 15px; /* Reduce space between input groups */
    }
  
    .spell-item {
      margin-bottom: 8px; /* Reduce space between spell items */
    }
  
    h2 {
      font-size: 1.5rem; /* Reduce heading size for small screens */
    }
  
    .description {
      font-size: 0.8em; /* Further reduce font size for descriptions */
    }
  }